.Carousel {
  margin: 0 auto;
  border: 5px solid black;
  border-radius: 25px;
  overflow: hidden;


  &__list {
    display: flex;
    list-style: none;
    padding: 0;

    transition: transform 1000ms;

    transform: translateX(-910px);

  }

  .button-wrapper {
    display: flex;
    justify-content: space-between;
    padding: 10px;
  }

  .button {
    border-radius: 5px;
  }
}
